import { AxiosRequestConfig } from "axios";
import axios from "./config";
import { Toast } from "vant";

export interface RespondData {
  code: number;
  result: any;
  timestamp: Date;
  message: string;
}

// HTTP
export default class Http {
  public static async request(params: AxiosRequestConfig) {
    const toast = Toast.loading({
      duration: 0,
      forbidClick: true,
      message: "..."
    });
    const res = await axios(params);
    console.log(" ", res.data);
    //  loading
    toast.clear();
    return res.data as RespondData;
  }

  /**
   * get
   * @param [url] 
   * @param [data] 
   * @returns Promise
   */
  public static get(url: string, params?: object, axiosConfig = {}) {
    return this.request({
      method: "GET",
      url,
      params,
      ...axiosConfig
    });
  }

  /**
   * put
   * @param [url] 
   * @param [data] 
   * @returns Promise
   */
  public static put(url: string, data?: object, axiosConfig = {}) {
    return this.request({
      method: "PUT",
      url,
      data,
      ...axiosConfig
    });
  }

  /**
   * post
   * @param [url] 
   * @param [data] 
   * @returns Promise
   */
  public static post(url: string, data?: object, axiosConfig = {}) {
    return this.request({
      method: "POST",
      url,
      data,
      ...axiosConfig
    });
  }

  /**
   * delete
   * @param [url] 
   * @param [params] 
   * @returns Promise
   */
  public static delete(url: string, params?: object, ...axiosConfig) {
    return this.request({
      method: "DELETE",
      url,
      params,
      ...axiosConfig
    });
  }
}
